




































































import {
  defineComponent,
  onBeforeMount,
  reactive,
  toRefs,
} from "@vue/composition-api"
import axios from "axios"

export default defineComponent({
  name: "Home",

  setup() {
    const state = reactive({
      system: {
        status: "None",
        degraded: false,
        incident: false,
      },
      status: {
        Normal: { icon: "mdi-check-circle", color: "green" },
        Degraded: { icon: "mdi-alert-circle", color: "orange" },
        Incident: { icon: "mdi-alert-circle", color: "red" },
        None: { icon: "mdi-checkbox-blank-circle-outline", color: "grey" },
      },
      services: [
        {
          icon: "mdi-post",
          title: "Moment",
          status: "None",
          desc: "",
          url: "https://x.moment.moe/.well-known/apollo/server-health",
        },
        {
          icon: "mdi-cube",
          title: "Auth",
          status: "None",
          desc: "",
          url: "https://xauth.hana.ooo/.well-known/apollo/server-health",
        },
        {
          icon: "mdi-code-braces-box",
          title: "Dev",
          status: "None",
          desc: "",
          url: "https://xdev.hana.ooo/.well-known/apollo/server-health",
        },
        {
          icon: "mdi-ab-testing",
          title: "Test",
          status: "None",
          desc: "",
          url: "https://xtest.hana.ooo/.well-known/apollo/server-health",
        },
        {
          icon: "mdi-link-variant",
          title: "URI",
          status: "None",
          desc: "",
          url: "https://xuri.hana.ooo/.well-known/apollo/server-health",
        },
        {
          icon: "mdi-school",
          title: "School",
          status: "None",
          desc: "",
          url: "https://xschool.hana.ooo/.well-known/apollo/server-health",
        },
        {
          icon: "mdi-hammer-wrench",
          title: "Alice",
          status: "None",
          desc: "",
          url: "https://xalice.hana.ooo/.well-known/apollo/server-health",
        },
        {
          icon: "mdi-sd",
          title: "Archive",
          status: "None",
          desc: "",
          url: "https://archive.moment.moe/.well-known/server-health",
        },
      ],
    })

    const getStatus = async (e: {
      url: string
      status: string
      desc: string
    }) => {
      try {
        await axios.get(e.url)

        e.status = "Normal"
      } catch (err) {
        state.system.degraded = true
        e.status = "Degraded"
        e.desc = err
      }
    }

    onBeforeMount(async () => {
      await Promise.all(state.services.map((e) => getStatus(e)))

      state.system.status = state.system.incident
        ? "Incident"
        : state.system.degraded
        ? "Degraded"
        : "Normal"

      const favicon = document.getElementById("favicon") as HTMLElement
      favicon.setAttribute("href", state.system.status + ".ico")
    })

    return {
      ...toRefs(state),
    }
  },
})
